import React, { useEffect } from "react";
import {
  CButton,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
} from "@coreui/react";
import "./index.scss";
import { useSelector } from "react-redux";
import { next, skuListUpdateCheck } from "./services.js";
import { totalQty } from "Page/StockEntry/GeneralServices/moduleServices";

const ModalsConfirmation = ({
  moduleEntry,
  confirmation,
  setConfirmation,
  save,
  submit,
  isSuccess,
  isLoading,
  moveAll,
  setAction,
  moduleName,
  noSkuChecking,
  singleViewData,
}) => {
  const dimention = document.documentElement.clientHeight;
  const height = dimention - 300;

  let buttonName = "Submit";

  if (confirmation.name === "save") buttonName = "Save";
  useEffect(() => {
    if (isSuccess) setConfirmation({ show: false, name: null });
  }, [isSuccess]);
  window.onkeydown = (e) =>
    e.keyCode === 27 ? setConfirmation({ show: false, name: null }) : null;

  useEffect(() => {
    setAction(confirmation.name);
  }, [confirmation]);

  let module = singleViewData && !moveAll ? singleViewData : moduleEntry;

  const areaDestinationName = module?.jobPost?.areaDestinationName;
  const warehouseDestinationName = module?.jobPost?.warehouseDestinationName;
  const areaName = module?.jobPost?.areaName;

  const skuListUpdate = skuListUpdateCheck(
    module?.jobPost.list,
    noSkuChecking,
    moduleName
  );
  return (
    <CRow
      className={"modalConfirmation " + (confirmation.show ? null : "d-none")}
    >
      <CCol>
        <CModal show={confirmation.show} closeOnBackdrop={false}>
          <CModalHeader className="px-4 pt-5 border-none">
            <CModalTitle className="text LatoBold">
              Confirmation{" "}
              <span>
                <i className="em em-pray"></i>
              </span>
            </CModalTitle>
            <label
              tabIndex="0"
              onKeyDown={(e) =>
                e.keyCode === 13
                  ? setConfirmation({ show: false, name: null })
                  : false
              }
              onClick={() => setConfirmation({ show: false, name: null })}
              className="sku-item iconU-modalClose"
            />
          </CModalHeader>
          <CModalBody className="px-4 py-0" style={{ minHeight: height }}>
            <p className="text mb-2">
              <span>{`Are you sure want to ${
                areaName == "Waste" ? "update" : "send"
              } this Item `}</span>
              <span
                className={` ${areaDestinationName ? null : "d-none"}`}
              >{` from`}</span>
              <span
                className={`green ${areaDestinationName ? null : "d-none"}`}
              >{` ${areaName} `}</span>
              <span>
                {moduleName == "outbound"
                  ? "from"
                  : moduleName === "repack"
                  ? ""
                  : "to"}{" "}
              </span>
              <span
                className={`green ${moduleName !== "repack" ? null : "d-none"}`}
              >{` ${
                moduleName === "stockTransfer"
                  ? warehouseDestinationName
                  : areaDestinationName
                  ? areaDestinationName
                  : areaName
              } `}</span>
            </p>
            <p className="text">
              Please double check this list, this record will store to database.
            </p>
            <div
              style={{
                minHeight: height,
                maxHeight: height,
                overflow: "auto",
                border: "1px solid var(--lineGrey)",
              }}
            >
              <table>
                <thead>
                  <tr style={{ borderBottom: "1px solid var(--lineGrey)" }}>
                    <th style={{ color: "var(--emptyTextField)" }}>SKU List</th>
                    <th
                      style={{
                        textAlign: "right",
                        color: "var(--emptyTextField)",
                      }}
                    >
                      Weight
                    </th>
                    <th
                      style={{
                        textAlign: "right",
                        color: "var(--emptyTextField)",
                      }}
                    >
                      Semi Waste
                    </th>
                    <th
                      style={{
                        textAlign: "right",
                        color: "var(--emptyTextField)",
                      }}
                    >
                      Waste
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {skuListUpdate?.map((data) => (
                    <tr style={{ borderBottom: "1px solid var(--lineGrey)" }}>
                      <td style={{ maxWidth: "160px" }}>{data.sku}</td>
                      <td
                        className="text-right py-3"
                        style={{ textAlign: "right" }}
                      >
                        {`${totalQty(data?.qty)}`}
                      </td>
                      <td
                        className="text-right text-nowrap"
                        style={{ textAlign: "right" }}
                      >
                        <span className="text-nowrap">{`${totalQty(
                          data?.swqty
                        )} `}</span>
                      </td>
                      <td className="text-right" style={{ textAlign: "right" }}>
                        {`${totalQty(data?.wqty)}`}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            <div className="px-0 pb-5 pt-0 justify-content-start border-none">
              <CButton
                tabIndex="0"
                onKeyDown={(e) =>
                  e.keyCode === 13
                    ? setConfirmation({ show: false, name: null })
                    : false
                }
                className="bg-background sku-item text no-shadow px-5 mt-4 mb-0 mx-0"
                onClick={() => setConfirmation({ show: false, name: null })}
              >
                Cancel
              </CButton>
              <CButton
                tabIndex="0"
                onKeyDown={(e) =>
                  e.keyCode === 13
                    ? isLoading
                      ? null
                      : confirmation.name === "save"
                      ? save()
                      : submit()
                    : false
                }
                className={`${
                  isLoading ? "bg-softGreen text" : "bg-green text-white"
                } no-shadow px-5 mt-4 sku-item mb-0 ml-3 mr-0`}
                onClick={() =>
                  isLoading
                    ? null
                    : confirmation.name === "save"
                    ? save()
                    : submit()
                }
              >
                {isLoading ? "Processing..." : buttonName}
              </CButton>{" "}
            </div>
          </CModalBody>
        </CModal>
      </CCol>
    </CRow>
  );
};

const ModalsCaptcha = ({
  show = false,
  setIsShowModal,
  submit,
  submitText = "Submit",
}) => {
  //random text
  var text = "";
  var characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
  var charactersLength = characters.length;
  var text_split = [];
  for (var i = 0; i < 3; i++) {
    let r = characters.charAt(Math.floor(Math.random() * charactersLength));
    text += r;
    text_split.push(r);
  }

  const selectorCsvFileName = useSelector((state) => state.csvFileName);
  const [isCaptcha, setIsCaptcha] = React.useState(text_split);
  const [isCaptchaText, setIsCaptchaText] = React.useState(text);
  const [isValidated, setIsValidated] = React.useState(false);
  const [isType, setIsType] = React.useState([]);

  //reset if visible = false
  useEffect(() => {
    if (show === true) {
      document.getElementById("t1").value = "";
      document.getElementById("t2").value = "";
      document.getElementById("t3").value = "";
    }
  }, [show]);

  return (
    <CRow className={show ? null : "d-none"}>
      <CCol>
        <CModal
          show={show}
          onClose={() => {
            setIsShowModal(false);
          }}
          size="lg"
          style={{ height: "400px" }}
        >
          <CModalHeader className="px-5 pt-5 border-none">
            <CModalTitle className="text LatoBold">Confirmation</CModalTitle>
          </CModalHeader>
          <CModalBody className="px-5 py-0">
            <p className="text mb-2">
              Are you sure want to upload <span className="green">Edited</span>{" "}
              {selectorCsvFileName}?
            </p>
            <p className="text">
              Once uploaded this record will store to databases.
            </p>
            <p className="text mb-2">
              Please type {isCaptchaText} on the Box to confirm upload file.
            </p>

            <div className="captcha-box">
              <input
                autocomplete="off"
                onKeyUp={(e) => {
                  next({
                    e,
                    state: 1,
                    isCaptcha,
                    setIsValidated,
                    setIsType,
                    isType,
                    isValidated,
                    submit,
                  });
                }}
                id="t1"
                type="text"
                style={{ textTransform: "uppercase" }}
                className="captcha-input"
                maxLength="1"
                placeholder={isCaptcha[0]}
              />
              <input
                autocomplete="off"
                onKeyUp={(e) => {
                  next({
                    e,
                    state: 2,
                    isCaptcha,
                    setIsValidated,
                    setIsType,
                    isType,
                    isValidated,
                    submit,
                  });
                }}
                id="t2"
                type="text"
                style={{ textTransform: "uppercase" }}
                className="captcha-input ml-0 mr-0"
                maxLength="1"
                placeholder={isCaptcha[1]}
              />
              <input
                autocomplete="off"
                onKeyUp={(e) => {
                  next({
                    e,
                    state: 3,
                    isCaptcha,
                    setIsValidated,
                    setIsType,
                    isType,
                    isValidated,
                    submit,
                  });
                }}
                id="t3"
                type="text"
                style={{ textTransform: "uppercase" }}
                className="captcha-input"
                maxLength="1"
                placeholder={isCaptcha[2]}
              />
            </div>
          </CModalBody>

          <CModalFooter className="px-5 pb-5 pt-0 justify-content-start border-none">
            <CButton
              className="bg-background text no-shadow px-5 mt-4 mb-0 mx-0"
              onClick={() => {
                setIsShowModal(false);
              }}
            >
              Cancel
            </CButton>
            <CButton
              className={
                (isValidated ? "bg-green" : "bg-emptyTextField disabled") +
                " no-shadow px-5 mt-4 mb-0 ml-3 mr-0"
              }
              onClick={() => {
                submit();
                setIsValidated(false);
              }}
            >
              {submitText}
            </CButton>{" "}
          </CModalFooter>
        </CModal>
      </CCol>
    </CRow>
  );
};

export { ModalsConfirmation, ModalsCaptcha };
